.particle-connect-form-contaier {
    width: 100%;
    height: 100%;
    position: relative
}

.particle-form-footer {
    font-size: 18px;
    font-weight: 600;
    width: 100%;
    color: var(--font-color1);

}

.particle-qrcode {
    width: 380px;
    height: 380px;
    box-sizing: border-box;
    border: 1px solid #E9EAEB;
    border-radius: 10px;
    margin-top: 30px;
    margin-bottom: 5px;
    background-color: #fff;
}

.particle-copy-uri {
    color: var(--font-color2);
    font-size: 16px;
    cursor: pointer;
    text-align: center;
    margin-bottom: 10px;
    width: 100%;

}

.particle-qrcode img {
    width: 100%;
    height: 100%;
}

.particle-loading>img {
    width: 68px;
    height: 68px;
    border-radius: 10px;
}

.particle-loading h3 {
    font-size: 18px;
    margin-bottom: 20px;
    font-size: 18px;
    font-weight: 800;
    margin-top: 10px;
    line-height: 30px;
    color: var(--font-color1);
}

.particle-loading p {
    font-size: 14px;
    font-weight: 500;
    line-height: 12px;
    color: var(--font-color2);

    text-align: center;
    margin: 0;

}

@keyframes loading-inner {
    0% {
        transform: rotate(0deg);
    }

    50% {
        transform: rotate(180deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

.particle-loading-img {
    width: 30px;
    height: 30px;
    margin-top: 20px;
    animation: loading-inner 1.5s linear infinite;
}

.particle-open-qrcode {
    width: 53px;
    height: 28px;
    background-color: rgba(0, 0, 0, 0.06);
    font-size: 12px;
    text-align: center;
    border-radius: 20px 20px 20px 20px;
    line-height: 28px;
    margin-left: 30px;
    cursor: pointer;
    border: 1px solid rgba(0, 0, 0, 0.1);
    color: #000;
    font-weight: 800;
}

.particle-tips {
    margin-top: 10px;
    font-size: 12px;
    color: var(--font-color2);
    width: 100%;
    padding: 0 40px;
    box-sizing: border-box;
    margin-bottom: 10px;
}