.particle-account-modal-container {
    z-index: 1998;
    visibility: hidden;
    border-radius: 0;
    overflow: hidden;
    position: absolute;
    right: -20px;
    top: 60px;
    width: 100vw;
    height: 100vh;
}

@media screen and (max-width:600px) {
    .particle-account-modal-container {
        z-index: 1998;
        visibility: hidden;
        border-radius: 0;
        overflow: hidden;
        position: initial !important;
        left: 0 !important;
        top: 0 !important;
        width: auto;
        height: auto;
        background-color: rgba(0, 0, 0, 0.39);
    }

    .particle-account-modal-box {
        position: absolute !important;
        right: 0;
        top: 60px;
    }
}

.particle-account-modal-container.show {
    visibility: initial;
}

@keyframes model-box-show {
    0% {
        transform: translateY(-20px);
        opacity: 0;
        height: auto;
    }

    100% {
        transform: translateY(0px);
        opacity: 1 !important;
        min-height: 100px;
        height: auto;
    }

}


.particle-account-modal-box {
    position: absolute;
    max-width: 100vw;
    width: 280px;
    height: 0;
    z-index: 1999;
    padding: 18px 22px;
    border: 1px solid var(--bg-color);
    background-color: var(--bg-color);
    border-radius: 18px;
    opacity: 0;
    transform: translateY(-20px);
    visibility: hidden;
    overflow: hidden;
    border: 1px solid var(--border-color);
    box-shadow: 0px 3px 6px rgba(137, 146, 175, 0.16);
}

.particle-account-modal-box.topLeft {
    left: 0;
    bottom: 60px;
}

.particle-account-modal-box.topRight {
    right: 0;
    bottom: 60px;
}

.particle-account-modal-box.bottomLeft {
    left: 0;
    top: 60px;
}

.particle-account-modal-box.bottomRight {
    right: 0;
    top: 60px;
}

.particle-account-modal-box.show {
    visibility: initial;
    animation: model-box-show .18s ease forwards;
}

.particle-account-modal-title {
    font-size: 18px;
    height: 60px;
    line-height: 60px;
    font-weight: 800;
    color: var(--font-color1);
    margin-left: 6px;
    width: 100%;

}

.particle-account-modal-close {
    font-size: 14px;
    position: absolute;
    right: 24px;
    top: 24px;
    cursor: pointer;
    background-size: cover;
    z-index: 9;
    transition: all 0.1s;
    color: var(--font-color1);

}

.particle-account-header {
    display: flex;
    align-items: center;
    padding: 0 0 16px 0;
    box-sizing: content-box;
    border-bottom: 1px solid #DCDFE6;
    width: 100%;
    margin-bottom: 17px;

}

.particle-avatar {
    height: 33px;
    width: 33px;
    margin-right: 0;
    border-radius: 50%;
    overflow: hidden;
}

.particle-account-address {
    margin-left: 7px;
    font-size: 17px;
    font-weight: 500;
    line-height: 20px;
    color: var(--font-color3);
    font-family: Apercu Pro;
    user-select: none;
}

.particle-account-info span {
    user-select: none;

}


.particle-logout:hover {
    transform: scale(1.02);
}

.particle-logout:active {
    opacity: 0.8;
}

.particle-icon-copy {
    width: 18px;
    height: 18px;
    margin-left: 10px;
    cursor: pointer;
}

.particle-link-item {
    width: 100%;
    height: 40px;
    opacity: 1;
    border-radius: 6px;
    transition: all 0.3s;
    font-size: 17px;
    font-weight: 500;
    line-height: 20px;
    color: var(--font-color1);
    padding: 0 10px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    cursor: pointer;
    font-family: Apercu Pro !important;
}

.particle-link-item .icon {
    font-size: 20px;
    font-weight: 600;
    margin-right: 15px;
    color: var(--font-color1);

}

.particle-link-item.red,
.particle-link-item.red .icon {
    color: #F92531 !important;
}

.particle-link-item.red:hover {
    background: rgba(249, 37, 49, 0.1) !important;
}

.particle-link-item:hover {
    background: var(--hover-color2);
}