.particle-chain-modal-container {
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.39);
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 1998;
    visibility: hidden;
    color: var(--font-color1);

}

.particle-chain-modal-container.show {
    visibility: initial;
}

@keyframes model-box-show {
    0% {
        transform: translateY(100px);
        opacity: 0;
    }

    100% {
        transform: translateY(0px);
        opacity: 1 !important;
    }

}

.particle-chain-modal-box {
    max-width: 100%;
    width: 350px;
    min-height: 100px;
    border: 1px solid var(--bg-color);
    background-color: var(--bg-color);
    border-radius: 21px;
    opacity: 0;
    transform: translateY(100px);
    visibility: hidden;
    /* position: fixed; */
    /* left: calc(50vw - 175px); */
    /* z-index: 1999; */
    overflow: hidden;
    padding: 16px;
    box-sizing: border-box;
}

.particle-chain-modal-box.show {
    visibility: initial;
    animation: model-box-show .18s ease forwards;
    opacity: 1;
}

.particle-chain-modal-title {
    font-size: 18px;
    height: 60px;
    line-height: 60px;
    font-weight: 800;
    color: var(--font-color1);
    margin-left: 6px;
    width: 100%;

}

.particle-chain-modal-close {
    width: 28px;
    height: 28px;
    border-radius: 50%;
    position: absolute;
    right: 15px;
    top: 15px;
    cursor: pointer;
    background-image: var(--close-icon);
    background-size: cover;
    z-index: 9;
    transition: all 0.1s;
}

.particle-modal-title {
    font-size: 18px;
    font-weight: 800;
    color: var(--font-color1);
    width: 100%;
    margin-bottom: 10px;
}

.particle-chain-list-box {
    width: 100%;
    max-height: 65vh;
    overflow-y: auto;
}


.particle-not-tips {
    padding: 0 10px 0 0;
    box-sizing: border-box;
    margin-top: 15px;
    color: var(--font-color2);
    font-size: 14px;
}

.particle-chain-list-item {
    height: 40px;
    width: 100%;
    border-radius: 12px;
    padding: 7px;
    box-sizing: border-box;
    font-weight: 700;
    color: var(--font-color1);
    margin-top: 4px;
    cursor: default;
}

.particle-disconnect-btn {
    color: #FF494A;
    height: 40px;
    width: 100%;
    border-radius: 12px;
    box-sizing: border-box;
    font-weight: 700;
    margin-top: 10px;
    cursor: default;
}

.particle-chain-list-item.item-hover:hover,
.particle-disconnect-btn:hover {
    background-color: var(--hover-color);
    cursor: pointer;
    transition: all 0.3s;

}


.particle-chain-list-item img {
    width: 28px;
    height: 28px;
    border-radius: 50%;
    margin-right: 10px;
}


.particle-chain-list-item.active {
    background-color: var(--active-bg-color) !important;
    color: var(--active-font-color) !important;
}

.particle-chain-active-connected {
    color: var(--active-font-color);
    font-size: 14px;
}

.particle-chain-active-confirm {
    font-weight: 400;
    font-size: 12px;
}

.particle-chain-active-confirm span {
    background-color: #FFD641;
    display: flex;
    width: 8px;
    height: 8px;
    margin-left: 6px;
    border-radius: 50%;
}

.particle-chain-active-connected {
    display: none;
    font-weight: 400;
    font-size: 12px;
}

.particle-chain-list-item.active .particle-chain-active-connected {
    display: flex;
}

.particle-chain-active-connected span {
    display: flex;
    width: 8px;
    height: 8px;
    background-color: #30E000;
    margin-left: 6px;
    border-radius: 50%;
}