.particle-connect-modal-container {
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.39);
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 1998;
    visibility: hidden;
    overflow: hidden;
}

.particle-connect-modal-container.show {
    visibility: initial;
}

@keyframes model-box-show {
    0% {
        transform: translateY(200px);
        opacity: 0;
    }

    100% {
        transform: translateY(0px);
        opacity: 1;
    }
}

.particle-connect-modal-box {
    width: 748px;
    max-width: 95vw;
    height: 526px;
    border: 1px solid var(--bg-color);
    background-color: var(--bg-color);
    border-radius: 20px;
    /* opacity: 0; */
    transform: translateY(200px);
    visibility: hidden;
    position: fixed;
    top: calc(50vh - 263px);
    left: calc(50vw - 374px);
    z-index: 1999;
    overflow: hidden;
}

.particle-connect-modal-box.show {
    visibility: initial;
    animation: model-box-show 0.18s ease forwards;
}

.particle-connect-modal-title {
    font-size: 18px;
    height: 60px;
    line-height: 60px;
    font-weight: 600;
    color: var(--font-color1);
    margin-left: 6px;
    width: 100%;
}

.particle-connect-modal-close {
    width: 28px;
    height: 28px;
    border-radius: 50%;
    position: absolute;
    right: 15px;
    top: 15px;
    cursor: pointer;
    background-image: var(--close-icon);
    background-size: cover;
    z-index: 9;
    transition: all 0.1s;
}

.particle-connect-modal-close:hover {
    transform: scale(1.1);
}

.particle-connect-methods-list {
    height: 100%;
    padding: 0 0px 0 22px;
    box-sizing: border-box;
}

.particle-connect-methods-form {
    width: 446px;
    max-width: 95vw;
    height: 526px;
    border-left: 1px solid #efefef;
}

.particle-connect-modal-group-name {
    padding: 10px 0;
    font-size: 14px;
    font-weight: 500;
    line-height: 12px;
    color: #868989;
    max-width: 100%;
    overflow-x: hidden;
    margin-left: 6px;
}

.particle-connect-modal-group-item {
    font-size: 17px;
    font-weight: 500;
    line-height: 12px;
    color: var(--font-color1);
    width: 260px;
    height: 41px;
    overflow: hidden;
    transition: all 0.3s;
    border-radius: 12px;
    cursor: pointer;
    margin-top: 4px;
}

.particle-connect-modal-group-item img {
    width: 30px;
    height: 30px;
}

.particle-group-icon-box {
    height: 30px;
    width: 30px;
    margin-left: 4px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin-right: 13px;
    margin-left: 6px;
    overflow: hidden;
    border-radius: 5px;
}

.particle-connect-modal-group-item.active {
    background-color: var(--active-bg-color);
    color: var(--active-font-color) !important;
    transform: scale(1) !important;
}

.particle-item-hover1:active {
    transform: scale(0.95);
}

.particle-item-hover1:hover {
    background-color: var(--hover-color);
    color: var(--font-color1);
}

.particle-connect-more {
    font-size: 14px;
    font-weight: 500;
    line-height: 16px;
    color: var(--font-color1);
    width: 100%;
    padding-right: 28px;
    box-sizing: border-box;
    width: 100%;
    height: 40px;
    cursor: pointer;
}

.particle-connect-more .particle-icon_more {
    width: 11.44px;
    height: 6.75px;
    margin-left: 4px;
    border-radius: 4px;
    transition: all 0.3s;
    background-image: var(--more-icon);
    background-size: cover;
}

.particle-connect-more .particle-icon_more.show {
    transform: rotate(180deg);
}

.particle-connect-modal-gourp-list {
    height: 460px;
    overflow-y: auto;
    padding-bottom: 20px;
    box-sizing: border-box;
}

.particle-connect-modal-gourp-list::-webkit-scrollbar-thumb {
    width: 8px;
    background: var(--scoll-bar-color);
    margin-right: 2px;
    border: none;
    border-radius: 10px;
}

.particle-no-auth-key {
    width: 100%;
    height: 100%;
    padding: 30px;
    box-sizing: border-box;
    font-size: 14px;
}

.particle-item-what {
    margin-top: 30px;
    margin-bottom: 28px;
    width: 100%;
}

.particle-no-auth-key h3 {
    font-size: 18px;
    width: 100%;
    margin-top: 0;
    width: 100%;
    font-weight: 600;
    text-align: center;
    color: var(--font-color1);
}

.particle-item-what h5 {
    font-size: 14px;
    font-weight: 500;
    line-height: 22px;
    color: var(--font-color1);
    max-width: 276px;
    margin: 0;
}

.particle-item-what p {
    font-size: 14px;
    font-weight: 400;
    color: var(--font-color2);
    margin: 0;
    line-height: 22px;
    max-width: 276px;
}

.particle-icon-what {
    width: 48px;
    height: 48px;
    margin-right: 26px;
    user-select: none;
    margin-left: 22px;
    pointer-events: none;
}

.particle-icon-back {
    display: none;
}

/* min-pc */
@media screen and (max-width: 768px) {
    .particle-connect-modal-box {
        width: 350px;
        left: calc(50vw - 175px);
    }

    .particle-connect-methods-list.hide {
        display: none;
    }

    .particle-connect-modal-title {
        text-align: center;
        padding-right: 22px;
        box-sizing: border-box;
    }

    .particle-connect-modal-group-item {
        /* min-width: 326px; */
    }

    .particle-connect-methods-form {
        display: none;
        max-width: 100%;
        box-sizing: border-box;
        border: none;
    }

    .particle-connect-methods-form.show {
        display: block;
        position: relative;
    }

    .particle-icon-back {
        display: block;
        position: absolute;
        left: 15px;
        top: 15px;
        cursor: pointer;
        z-index: 9;
        background-image: var(--back-icon);
        width: 28px;
        height: 28px;
        background-size: 100% 100%;
    }

    .particle-qrcode {
        max-width: 320px;
        max-height: 320px;
        padding: 10px;
    }
}

.-cbwsdk-css-reset {
    display: none;
}
